<template>
  <div class="wrapper" id="app">
    <div class="contents-wrapper">
      <page-header />
      <router-view/>
      <page-footer />
    </div>
  </div>
</template>

<script>
import PageHeader from './components/PageHeader'
import PageFooter from './components/PageFooter'
export default {
  name: 'App',
  components: {
    PageHeader,
    PageFooter
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.css';
.wrapper {
  padding: 20px 0;
  background: #faf2e5;
  position: relative;
  display: flex;
  justify-content: center;
  min-width: 990px;
  max-width: 100%;
  min-height: 350px;
  margin: 0 auto;
  .contents-wrapper {
    min-width: 990px;
    max-width: 100%;
    min-height: 350px;
    background: #fff;
    box-shadow: 0 10px 10px 0 rgba(0,0,0,0.2);
  }
}
</style>



